import Briefcase from "@/components/icons/brieftcase";
import SocialLinks from "@/components/SocialLinks";
import ButtonNavbar from "@/components/ButtonNavbar";

export default function Header() {
    return (
        <header className="bg-opacity-50 backdrop-blur-sm bg-neutral-primary fixed w-full z-20 top-0 start-0 border-b border-orange-200/20 pt-2 pb-2">
            <div className="max-w-screen-xl flex flex-wrap items-center gap-4 justify-between mx-auto pl-2">
                <div>
                    <Briefcase size={36} color="white" />
                </div>

                <div className="flex items-center justify-center gap-4">
                    <ButtonNavbar name="Home" href="/#" />
                    <ButtonNavbar name="Sobre mí" href="" />
                    <ButtonNavbar name="Proyectos" href="/#projects" />
                </div>

                <div className="pr-2">
                    <SocialLinks />
                </div>
            </div>
        </header>
    );
}